@import '../base/variables';
@import '../base/layout';

.search-result {
  position: relative;

  .search-result__inner-container {
    display: table;

    box-sizing: border-box;
    width: 100%;
  }

  .search-result__image-container {
    display: table-cell;

    box-sizing: border-box;
    min-width: 200px;

    vertical-align: top;

    @media @small-screen {
      display: table-row;

      width: 100%;
      margin-bottom: 20px;
    }
  }

  .search-result__image {
    min-width: 200px;
    max-width: 100%;

    @media @small-screen {
      display: block;

      width: 100%;
      height: auto;
      margin: 0 auto;
    }
  }

  .search-result__content {
    display: table-cell;

    box-sizing: border-box;
    width: 80%;
    padding-left: 20px;

    vertical-align: top;

    @media @small-screen {
      display: table-row;

      width: 100%;
    }
  }

  .search-result__title-container {
    padding-bottom: 20px;

    text-align: left;

    @media @small-screen {
      padding-top: 20px;

      text-align: center;
    }
  }

  .search-result__title {
    display: inline;

    font-weight: bold;

    text-transform: uppercase;

    color: @dark-gray-1;
  }

  .search-result__date {
    display: inline-block;

    font-weight: normal;

    color: @dark-gray-1;

    [class*='fa-'] {
      padding: 5px;
    }
  }

  .search-result__teaser-container {
    padding-bottom: 20px;

    @media @medium-screen, @small-screen {
      font-size: 0.9em;
    }
  }

  .search-result__link-container {
    display: inline-block;
  }
}
